import { NextRouter, useRouter } from 'next/router';

export const FixRoutePrivate = (router: NextRouter) =>
  router.asPath
    .replace(
      `dashboard/services/${router.query.service}/${router.query.id}`,
      ''
    )
    .replace('//', '/');

export const FixRoutePublic = (route: NextRouter) =>
  route.asPath
    .replace(`feed/${route.query.service}`, '')
    .replace(`dashboard/services/${route.query.service}/${route.query.id}`, '')
    .replace('//', '/');

export const GetRoutePrivate = (router: NextRouter) =>
  `/dashboard/services/${router.query.service}/${router.query.id}`;

export const GetRoutePublic = (router: NextRouter) =>
  `/feed/${router.query.service}`;

const useRouterPrivate = () => {
  const router = useRouter();
  const route = GetRoutePrivate(router);

  return {
    next: router,
    asPath: router.asPath,
    asPathRelative: FixRoutePrivate(router),
    push: (path: string) => router.push(`${route}${path}`),
    FixRoutePrivate,
    FixRoutePublic,
    GetRoutePrivate,
  };
};

export const useRouterPublic = () => {
  const router = useRouter();
  const route = GetRoutePublic(router);

  return {
    next: router,
    asPath: router.asPath,
    asPathRelative: FixRoutePublic(router),
    push: (path: string) => router.push(`${route}${path}`),
    FixRoutePrivate,
    FixRoutePublic,
    GetRoutePrivate,
  };
};

export default useRouterPrivate;
